@font-face {
  font-family: "Golos Text";
  src: url("GolosText-DemiBold.eot");
  src: local("Golos Text DemiBold"), local("GolosText-DemiBold"), url("GolosText-DemiBold.eot?#iefix") format("embedded-opentype"), url("GolosText-DemiBold.woff2") format("woff2"), url("GolosText-DemiBold.woff") format("woff"), url("GolosText-DemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Golos Text";
  src: url("GolosText-Regular.eot");
  src: local("Golos Text Regular"), local("GolosText-Regular"), url("GolosText-Regular.eot?#iefix") format("embedded-opentype"), url("GolosText-Regular.woff2") format("woff2"), url("GolosText-Regular.woff") format("woff"), url("GolosText-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body,
h1,
div,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Golos Text", "Arial", sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  padding: 0;
}

select {
  appearance: none;
}

select:focus,
input:focus,
button:focus {
  outline: none;
  border: none;
}

div {
  outline: none;
}

select::-ms-expand {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  padding: 16px 16px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__left {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__logo {
  width: 129px;
  height: 20px;
}
.header__right {
  display: flex;
  gap: 20px;
  align-items: center;
}
.header__icon {
  width: 20px;
}

.toggle-menu {
  width: 20px;
  height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
}

.line {
  width: 20px;
  height: 2px;
  border-radius: 3px;
  background-color: #000000;
  transition: transform 0.2s ease-out;
}

.toggle .line1 {
  background-color: #000000;
  transform: scale(0.9) rotateZ(-45deg) translate(-4px, 4px);
}

.toggle .line2 {
  display: none;
}

.toggle .line3 {
  background-color: #000000;
  transform: scale(0.9) rotateZ(45deg) translate(-4px, -4px);
}

.toggle .toggle-menu {
  background-color: transparent;
}

@media (min-width: 768px) {
  .nav {
    display: none;
  }
}
.nav__name {
  color: #000000;
  font-size: 24px;
  text-decoration: none;
  padding-bottom: 15px;
}
.nav__button {
  border: none;
  background-color: transparent;
  box-shadow: none;
}
.nav__overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #f9f4a8;
  top: 0;
  left: 0;
  right: -100%;
  bottom: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}
.nav__content {
  background-color: #f9f4a8;
  padding: 42px 25px 25px;
  height: 100%;
  width: 260px;
  position: absolute;
  margin: 0 auto;
  z-index: 99;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  right: -260px;
  transition: all 0.5s;
}
.nav__content--open {
  right: 0;
  transition: all ease 0.5s;
}
.nav__logo {
  width: 210px;
}
.nav__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  padding-top: 30px;
}
.nav__signature {
  width: 110px;
  color: #ffffff;
  font-size: 13px;
}

.overlay-visible {
  visibility: visible;
  opacity: 1;
}

html {
  scroll-behavior: smooth;
}

.container {
  min-width: 320px;
  min-height: auto;
}
@media (min-width: 768px) {
  .container {
    min-width: 100%;
  }
}
@media (min-width: 1200px) {
  .container {
    margin: 0 auto;
    min-width: 100%;
  }
}